import React, { useState } from "react"
import { RouteComponentProps } from "react-router-dom"
import { createStyles, makeStyles } from "@material-ui/core/styles"
import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
  Grid,
  IconButton,
  Paper,
  TextField,
  Typography,
} from "@material-ui/core"
import { Archive, PauseCircleOutline, PlayCircleOutline, SkipNext, Stop } from "@material-ui/icons"
import { ABTestStatusType, ExecutionStatusType } from "../../model/ExecutionStatusType"
import EmptyDivider from "../../components/Divider/EmptyDivider"
import VariationTab from "./tabs/VariationTab"
import ExperimentState from "./ExperimentState"
import ResultTab from "./tabs/ResultTab"
import AllowlistTab from "./tabs/AllowlistTab"
import MetricTab from "./tabs/MetricTab"
import { useRecoilValue } from "recoil/dist"
import { envProfileState } from "../../states/EnvProfile"
import { Alert } from "@material-ui/lab"
import {AntTab, AntTabs} from "../../components/AntTab/AntTab";

const useStyles = makeStyles((theme) =>
  createStyles({
    root: {
      flexGrow: 1,
    },
    paper: {
      padding: theme.spacing(2),
      color: theme.palette.text.secondary,
    },
    buttonGroup: {
      textAlign: "right",
    },
    button: {
      margin: theme.spacing(1),
    },
    start: {
      padding: 0,
      marginLeft: 8,
      marginRight: 8,
      color: theme.palette.success.main,
      "&:hover": {
        color: theme.palette.success.dark,
      },
    },
    pause: {
      padding: 0,
      marginLeft: 8,
      marginRight: 8,
    },
  })
)




interface Props {
  key: string
}

export default function ExperimentDetail({ match }: RouteComponentProps<Props>) {
  const [status, setStatus] = useState<ExecutionStatusType>("READY")
  const [pauseTyping, setPauseTyping] = useState("")
  const [value, setValue] = useState(0)
  const [pauseModalOpen, setPauseModalOpen] = useState(false)
  const env = useRecoilValue(envProfileState)
  //TODO
  const [abTestStatus] = useState<ABTestStatusType>(match.params.key === "5521" ? "DRAFT" : "EXECUTED")

  const handleChange = (event: React.ChangeEvent<{}>, newValue: number) => {
    setValue(newValue)
  }

  const start = () => setStatus("RUNNING")
  const pause = () => {
    setStatus("PAUSED")
    setPauseModalOpen(false)
  }

  const statusButton = () => {
    switch (status) {
      case "READY":
        return (
          <IconButton edge="end" aria-label="start" color="primary" onClick={start} className={classes.start}>
            <PlayCircleOutline />
          </IconButton>
        )
      case "RUNNING":
        return (
          <IconButton
            edge="end"
            aria-label="pause"
            color="secondary"
            onClick={() => setPauseModalOpen(true)}
            className={classes.pause}
          >
            <PauseCircleOutline />
          </IconButton>
        )
      case "PAUSED":
        return (
          <IconButton edge="end" aria-label="pause" color="primary" onClick={start} className={classes.start}>
            <SkipNext />
          </IconButton>
        )
    }
  }
  const classes = useStyles()
  const { params } = match
  return (
    <div className={classes.root}>
      <Grid container spacing={3}>
        <Grid item xs={12}>
          <Paper className={classes.paper}>
            <Grid container spacing={3}>
              <Grid item xs={7}>
                <Typography variant={"subtitle1"}>{`#${params.key}`}</Typography>
                <Typography
                  suppressContentEditableWarning={true}
                  variant={"h5"}
                  contentEditable={true}
                  onBlur={(e) => {
                    //TODO
                    console.log(e.target.textContent)
                  }}
                >
                  Recommendation logic v2
                </Typography>
                <Typography
                  suppressContentEditableWarning={true}
                  variant={"subtitle1"}
                  contentEditable={true}
                  onBlur={(e) => {
                    //TODO
                    console.log(e.target.textContent)
                  }}
                >
                  A/B Test Description is
                </Typography>
              </Grid>
              <Grid item xs={5}>
                <div className={classes.buttonGroup}>
                  <Button
                    variant="contained"
                    color="default"
                    className={classes.button}
                    startIcon={<Stop />}
                    onClick={() => {}}
                  >
                    Complete
                  </Button>
                  <Button
                    variant="contained"
                    color="default"
                    className={classes.button}
                    startIcon={<Archive />}
                    onClick={() => {}}
                  >
                    Archive
                  </Button>
                </div>
              </Grid>
              <Grid item xs={8}>
                <Grid container spacing={3}>
                  <Grid item xs={3}>
                    <Typography variant={"subtitle2"}>Status</Typography>
                  </Grid>
                  <Grid item xs={9}>
                    <ExperimentState type={status} />
                    {statusButton()}
                    <Dialog
                      open={pauseModalOpen}
                      onClose={() => setPauseModalOpen(false)}
                      aria-labelledby="form-dialog-title"
                    >
                      <DialogTitle id="form-dialog-title">Pause this a/b test?</DialogTitle>
                      <DialogContent>
                        <DialogContentText>This will pause the Recommendation logic v2 in the {env}</DialogContentText>
                        {env === "production" && (
                          <Alert severity="warning">
                            After pausing a/b test in the production environment, every users will not be allocated into
                            any variations.
                          </Alert>
                        )}
                        <EmptyDivider />
                        <DialogContentText>Type the environment name or a/b test key to confirm.</DialogContentText>
                        <TextField
                          margin="dense"
                          id="name"
                          value={pauseTyping}
                          onChange={(v) => setPauseTyping(v.target.value)}
                          label={env}
                          type="text"
                        />
                      </DialogContent>
                      <DialogActions>
                        <Button onClick={() => setPauseModalOpen(false)} color="primary">
                          Cancel
                        </Button>
                        <Button disabled={pauseTyping !== env} onClick={pause} color="primary">
                          Pause
                        </Button>
                      </DialogActions>
                    </Dialog>
                  </Grid>
                  <Grid item xs={3}>
                    <Typography variant={"subtitle2"}>Traffic Allocation</Typography>
                  </Grid>
                  <Grid item xs={9}>
                    0%
                  </Grid>
                </Grid>
              </Grid>
              <Grid item xs={4} />
              <Grid item xs={8}></Grid>
              <Grid item xs={4} />
            </Grid>
            <EmptyDivider />
            <AntTabs value={value} onChange={handleChange} aria-label="ant example">
              <AntTab label={abTestStatus === "DRAFT" ? "Metric" : "Result"} />
              <AntTab label="Allowlist" />
              <AntTab label="Variation" />
            </AntTabs>
            {value === 0 && abTestStatus === "DRAFT" && <MetricTab />}
            {value === 0 && abTestStatus === "EXECUTED" && <ResultTab />}
            {value === 1 && <AllowlistTab />}
            {value === 2 && <VariationTab />}
          </Paper>
        </Grid>
      </Grid>
    </div>
  )
}
