import React, { ChangeEvent, useState } from "react"
import { makeStyles } from "@material-ui/core/styles"
import EmptyDivider from "../../../components/Divider/EmptyDivider"
import { Button, Chip, Grid, TextareaAutosize, Typography } from "@material-ui/core"
import { Face as FaceIcon } from "@material-ui/icons"
import SuccessSnackbar from "../../../components/Snackbar/SuccessSnackbar"

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  margin: {
    margin: theme.spacing(0.5),
  },
  createButtonWrap: {
    textAlign: "right",
  },
  button: {
    margin: theme.spacing(1),
  },
  variation: {
    border: "1px solid #aaa",
  },
  textArea: {
    verticalAlign: "middle",
  },
}))

interface Props {}

export default function AllowlistTab(props: Props) {
  const classes = useStyles()
  const [saved, setSaved] = useState(false)
  const [variations, setVariations] = useState<{ key: string; users: string[] }[]>([
    { key: "A", users: [] },
    { key: "B", users: [] },
  ])
  const [textAreaValues, setTextAreaValues] = useState<string[]>(["", ""])

  const onChangeHandler = (index: number) => (event: ChangeEvent<HTMLTextAreaElement>) => {
    const value = event.target.value
    if (value.trim() === ",") return
    const match = value.match(/,/g)
    if (match && match.length >= 2) {
      addUserList(
        index,
        value.split(",").map((v) => v.trim())
      )
    } else {
      textAreaValues[index] = value.trim()
      setTextAreaValues([...textAreaValues])
      addUser(index, value)
    }
  }
  const addUserList = (index: number, values: string[]) => {
    variations[index].users = [...variations[index].users]
    values.forEach((v) => {
      variations[index].users.push(v)
    })
    setVariations([...variations])
  }

  const addUser = (index: number, value: string) => {
    if (!value.includes(",")) return false

    variations[index].users = [...variations[index].users, value.replace(",", "")]
    setVariations([...variations])
    textAreaValues[index] = ""
    setTextAreaValues([...textAreaValues])
  }

  const onRemoveHandler = (variationIndex: number, targetIndex: number) => () => {
    variations[variationIndex].users = variations[variationIndex].users.filter((user, i) => {
      return targetIndex !== i
    })
    setVariations([...variations])
  }

  const onRemoveByKeyCodeHandler = (index: number) => ({ keyCode }: React.KeyboardEvent<HTMLTextAreaElement>) => {
    console.log(index, textAreaValues[index])
    if (keyCode !== 8) return
    if (!!textAreaValues[index]) return
    variations[index].users.pop()
    setVariations([...variations])
  }

  const onClearHandler = (index: number) => () => {
    variations[index].users = []
    setVariations([...variations])
  }

  const save = () => {
    variations.forEach((variation) => {
      console.log(variation.key, variation.users)
    })
    setSaved(true)
  }

  const onCloseHandler = () => setSaved(false)

  return (
    <div className={classes.root}>
      <EmptyDivider size={"small"} />
      <Grid container spacing={3}>
        <Grid item xs={9}>
          <Typography variant={"h6"}>Allowlist</Typography>
          <Typography variant={"caption"}>
            Manually assign users into running variations using the whitelist below.
          </Typography>
        </Grid>
        <Grid item xs={3}>
          <div className={classes.createButtonWrap}>
            <Button variant="contained" color="primary" className={classes.button} onClick={save}>
              Save
            </Button>
          </div>
        </Grid>
        <Grid item xs={12}>
          {variations.map(({ key, users }, variationIndex) => {
            return (
              <Grid container alignItems={"flex-end"} key={key}>
                <Grid item xs={4}>
                  <Typography variant={"subtitle1"}>Variation {key}</Typography>
                </Grid>
                <Grid item xs={8}>
                  <div className={classes.createButtonWrap}>
                    <span>{users.length} Users</span>
                    <Button
                      size={"small"}
                      variant="outlined"
                      className={classes.button}
                      onClick={onClearHandler(variationIndex)}
                    >
                      Clear
                    </Button>
                  </div>
                </Grid>
                <Grid item xs={12}>
                  <div className={classes.variation}>
                    {users.map((user, index) => (
                      <Chip
                        className={classes.margin}
                        key={index}
                        variant="outlined"
                        size="small"
                        label={user}
                        icon={<FaceIcon />}
                        onDelete={onRemoveHandler(variationIndex, index)}
                      />
                    ))}
                    <TextareaAutosize
                      onKeyDown={onRemoveByKeyCodeHandler(variationIndex)}
                      onChange={onChangeHandler(variationIndex)}
                      value={textAreaValues[variationIndex]}
                      className={classes.textArea}
                    />
                  </div>
                </Grid>
              </Grid>
            )
          })}
        </Grid>
      </Grid>
      <SuccessSnackbar open={saved} message={"Saved successfully"} onClose={onCloseHandler} />
    </div>
  )
}
